.answer {
   display: flex;
   justify-content: center;
   gap: 77px;
   justify-content: center;
   @media (max-width: 992px){
      flex-direction: column;
   }
   &__item {
      @media (max-width: 992px){
         max-width: 600px !important;
         margin: 0 auto;
      }
      &:first-child{
         max-width: 487px;
         width: 100%;
      }
      &:nth-child(2){
         max-width: 540px;
         width: 100%;
      }
   }
   &__title {
      margin-bottom: 40px;
   }
   &__text {
      color: rgba(14, 14, 44, 0.6) !important;
      font-weight: 400 !important;
   }
   .accordion {
      display: flex;
      justify-content: center;
      align-items: center;
      flex-direction: column;
      gap: 34px;
      &__item {
         border: 2px solid #3B71FE;
         border-radius: 4px;
         padding: 0 19px;
         width: 100%;
         &.show{
            .line1{
               transform: rotate(90deg);
               transition: all .3s linear;
            }
            .accordion__content{
               opacity: 1;
               visibility: visible;
               height: auto;
               padding: 10px 0 39px;
               transition: all .3s ease;
            }
         }
      }
      &__header {
         cursor: pointer;
         padding: 19px 0;
         display: flex;
         align-items: center;
         justify-content: space-between;
         gap: 15px;
      }
      &__btn {
         position: relative;
         max-width: 14px;
         width: 100%;
         height: 14px;
         .line1 {
            position: absolute;
            top: 0;
            bottom: 0;
            left: 50%;
            transform: translate(-50%, 0);
            height: 100%;
            width: 2px;
            background: #3B71FE;
            transition: all .3s linear;
         }
         .line2 {
            position: absolute;
            left: 0;
            right: 0;
            top: 50%;
            transform: translate(0, -50%);
            width: 100%;
            height: 2px;
            background: #3B71FE;
         }
      }
      &__content {
         opacity: 0;
         visibility: hidden;
         height: 0;
         transition: all .3s ease;
      }
   }
}