.section__process{
   padding: 0 0 314px !important;
   @media (max-width: 992px){
      padding: 0 0 160px !important;
   }
   @media (max-width: 768px){
      padding: 0 0 120px !important;
   }
}
.process {
   &__header {
      display: flex;
      align-items: center;
      gap: 33px;
      margin-bottom: 193px;
      @media (max-width: 992px){
         flex-direction: column;
         gap: 10px;
         margin-bottom: 100px;
      }
      @media (max-width: 480px){
         margin-bottom: 41px;
      }
   }
   &__text {
      color: rgba(14, 14, 44, 0.6) !important;
      font-weight: 400 !important;
      margin-bottom: 0 !important;
   }
   &__body {
      display: flex;
      justify-content: center;
      align-items: center;
      gap: 27px;
      @media (max-width: 992px){
         flex-wrap: wrap;
         gap: 50px;
      }
   }
   &__item {
      position: relative;
      max-width: 250px;
      width: 100%; 
      &::after{
         position: absolute;
         width: 100%;
         height: auto;
         left: 50%;     
         @media (max-width: 992px){
            display: none;
         }   
      }
      &:nth-child(odd):after{
         content: url(..//img/process-arrow.svg);
         top: calc(100% + 14px);
      }
      &:nth-child(2)::after{
         content: url(..//img/process-arrow2.svg);
         top: -90%;
      }
   }
   &__item-header {
      display: flex;
      align-items: center;
      gap: 16px;
      margin-bottom: 10px;
   }
   &__item-title {
      font-weight: 700;
      font-size: 30px;
      line-height: 171.5%;
      color: #3b71fe;
   }
   &__item-subtitle{
      color: #0E0E2C !important;
      font-weight: 600 !important;
      margin-bottom: 0 !important;
      @media (max-width: 480px){
         font-size: 22px !important;
      }
   }
}
