.offers {
   display: grid;
   grid-template-columns: repeat(4, 1fr);
   grid-template-rows: repeat(2, 1fr);
   grid-column-gap: 39px;
   grid-row-gap: 75px;
   @media (max-width: 992px) {
      grid-template-columns: repeat(3, 1fr);
      grid-template-rows: repeat(3, 1fr);
      grid-column-gap: 20px;
      grid-row-gap: 40px;
   }
   @media (max-width: 768px){
      grid-template-columns: repeat(2, 1fr);
      grid-template-rows: repeat(4, 1fr);
      grid-row-gap: 20px;
   }
   @media (max-width: 480px){
     display: flex;
     flex-direction: column;
     gap: 20px;
   }
   &__header {
      grid-area: 1 / 1 / 2 / 3;
   }
   &__title {
      @media (max-width: 350px){
         font-size: 18px;
      }
   }
   &__item {
      display: flex;
      flex-direction: column;
      gap: 14px;
      align-items: flex-start;
      @media (max-width: 480px){
         grid-area: auto !important;
      }
      &:first-child {
         grid-area: 1 / 3 / 2 / 4;
      }
      &:nth-child(2) {
         grid-area: 1 / 4 / 2 / 5;
         @media (max-width: 992px) {
            grid-area: 2 / 1 / 3 / 2;
         }
      }
      &:nth-child(3) {
         grid-area: 2 / 1 / 3 / 2;
         @media (max-width: 992px) {
            grid-area: 2 / 2 / 3 / 3;
         }
         @media (max-width: 768px){
            grid-area: 3 / 1 / 4 / 2; 
         }
      }
      &:nth-child(4) {
         grid-area: 2 / 2 / 3 / 3;
         @media (max-width: 992px) {
            grid-area: 2 / 3 / 3 / 4;
         }
         @media (max-width: 768px){
            grid-area: 3 / 2 / 4 / 3; 
         }
      }
      &:nth-child(5) {
         grid-area: 2 / 3 / 3 / 4;
         @media (max-width: 992px) {
            grid-area: 3 / 1 / 4 / 2;
         }
         @media (max-width: 768px){
            grid-area: 4 / 1 / 5 / 2;
         }
      }
      &:nth-child(6) {
         grid-area: 2 / 4 / 3 / 5;
         @media (max-width: 992px) {
            grid-area: 3 / 2 / 4 / 3;
         }
         @media (max-width: 768px){
            grid-area: 4 / 2 / 5 / 3;
         }
      }
   }
   &__item-title {
      font-weight: 700;
      @media (max-width: 480px){
         font-size: 22px;
      }
   }
}
