.testimonial {
   &__header {
      max-width: 730px;
      width: 100%;
      margin-bottom: 61px;
      @media (max-width: 480px){
         margin-bottom: 8px;
      }
   }
   &__swiper {
      .swiper__buttons {
         display: flex;
         align-items: center;
         gap: 48px;
         margin-top: 20px;
         justify-content: center;
      }
      .swiper__btn {
         width: 48px;
         height: 48px;
         border: 2px solid #3b71fe;
         border-radius: 50%;
         display: flex;
         justify-content: center;
         align-items: center;
         cursor: pointer;
         transition: all 0.3s ease;
         path {
            transition: all 0.3s ease;
         }
         &:hover {
            background: #3b71fe;
            transition: all 0.3s ease;
            path {
               stroke: #fff;
               transition: all 0.3s ease;
            }
         }
      }
      .swiper-prev {
         svg {
            transform: rotate(-180deg);
         }
      }
      .slide {
         padding: 49px 28px 72px 66px;
         @media (max-width: 992px){
            padding: 55px 16px;
         }
         @media (max-width: 350px){
            padding: 40px 10px;
         }
         &__info {
            display: flex;
            align-items: center;
            gap: 8px;
            margin-top: 32px;
         }
         &__info-title {
            font-weight: 600;
            font-size: 18px;
            line-height: 27px;
            color: #0e0e2c;
         }
         &__info-text {
            font-weight: 500;
         }
      }
   }
}
