.section__team-about{
   .slide{
      flex-direction: column;
      gap: 0 !important;
   }
   .team{
      display: flex;
      gap: 30px;
      @media (max-width: 768px){
         flex-direction: column;
         gap: 73px;
         .swiper, .swiper-container{
            margin: 0 !important;
         }
      }
      &__header{
         min-width: 352px;
         width: 100%;
      }
      .swiper__buttons{
         display: none;
         @media (max-width: 1122px){
            display: flex;
         }
      }
   }
}
.team {
   &__swiper {
      .slide {
         display: flex;
         justify-content: center;
         align-items: center;
         gap: 135px;
         @media (max-width: 992px) {
            gap: 28px;
            .slide__content {
               max-width: 420px;
            }
            img {
               width: 100%;
            }
         }
         @media (max-width: 768px) {
            flex-direction: column;
            .slide__content {
               max-width: 100%;
            }
         }
         &__title {
            margin-bottom: 28px;
            @media (max-width: 480px) {
               margin-bottom: 24px;
            }
         }
         &__text {
            color: rgba(14, 14, 44, 0.6) !important;
         }
         &__info-title {
            color: #0e0e2c ;
            font-weight: 700;
            margin-bottom: 0;
            margin-top: 16px;
            @media (max-width: 480px) {
               font-size: 18px;
            }
         }
         &__info-text {
            font-size: 18px;
            @media (max-width: 480px) {
               font-size: 16px;
            }
         }
      }
      .swiper__buttons {
         display: flex;
         align-items: center;
         gap: 48px;
         justify-content: flex-end;
         margin-top: 20px;
         @media (max-width: 768px){
            justify-content: center;
         }
      }
      .swiper__btn {
         width: 48px;
         height: 48px;
         border: 2px solid #3b71fe;
         border-radius: 50%;
         display: flex;
         justify-content: center;
         align-items: center;
         cursor: pointer;
         transition: all .3s ease;
         path{
            transition: all .3s ease;
         }
         &:hover{
            background: #3b71fe;
            transition: all .3s ease;
            path{
               stroke: #fff;
               transition: all .3s ease;
            }
         }
      }
      .swiper-prev{
         svg{
            transform: rotate(-180deg);
         }
      }
   }
}
