footer {
   background: rgba(160, 164, 171, 0.1);
}
.footer {
   display: flex;
   justify-content: space-between;
   padding: 53px 0 26px;
   @media (max-width: 1122px){
      gap: 40px;
   }
   @media (max-width: 480px){
      gap: 20px;
      padding: 33px 0 79px;
   }
   @media (max-width: 400px){
      flex-direction: column;
      justify-content: center;
      align-items: center;
      text-align: center;
   }
   &__logo {
      margin-bottom: 16px;
   }
   &__item {
      display: flex;
      gap: 31px;
      @media (max-width: 1122px){
         gap: 20px;
      }
      @media (max-width: 992px){
         flex-direction: column;
         gap: 30px;
      }
   }
   &__info {
      max-width: 255px;
      width: 100%;
   }
   &__text {
      @media (max-width: 768px){
         font-size: 14px;
      }
   }
   a.footer__text{
      transition: all .3s ease;
      img{
         transition: all .3s ease;
      }
      &:hover{
         transition: all .3s ease;
         color: rgba(14, 14, 44, 1);
         img{
            transition: all .3s ease;
            transform: translate(-5px, 0);
         }
      }
   }
   &__menu {
      @media (max-width: 480px){
         display: none;
      }
   }
   &__title {
      font-weight: 600;
      font-size: 18px;
      line-height: 171.5%;
      color: #0e0e2c;
      margin-bottom: 25px;
      @media (max-width: 992px){
         margin-bottom: 15px;
      }
   }
   &__list {
      display: flex;
      flex-direction: column;
      gap: 16px;
      @media (max-width: 992px){
         gap: 8px;
      }
   }
   &__list-item {
      display: flex;
      align-items: center;
      gap: 8px;
   }
   &__contact {
      @media (max-width: 1122px){
         max-width: 252px;
      }
   }
   &__form {
      margin-top: 16px;
      display: flex;
      height: 48px;
      max-width: 252px;
      width: 100%;
      border: 1px solid #767676;
      border-radius: 4px;
      transition: all .3s ease;
      background: #ffffff;
      &:focus-within{
         transition: all .3s ease;
         border: 1px solid #0e0e2c;
      }
      @media (max-width: 400px){
         max-width: 100%;
      }
   }
   &__form-input {
      height: 100%;
      width: 100%;
      font-size: 14px;
      line-height: 1.5;
      color: #0e0e2c;
      padding: 0 16px;
      border: none;
      &::placeholder {
         color: #b1b1b1;
      }
      &::-moz-placeholder {
         color: #b1b1b1;
         opacity: 1;
      }
   }
   &__form-btn {
      height: 100%;
      padding: 0 12px;
      display: flex;
      align-items: center;
      justify-content: center;
      border: none;
      cursor: pointer;
   }
}
.copy {
   padding: 18px 0;
   background: #0e0e2c;
   border: 1px solid #0e0e2c;
   &__text {
      font-weight: 600;
      font-size: 16px;
      line-height: 171.5%;
      color: #ffffff;
      @media (max-width: 480px){
         text-align: center;
      }
   }
}
