.help {
   &__title {
      font-weight: 700;
      font-size: 24px;
      line-height: 171.5%;
      color: #0e0e2c;
      margin-bottom: 48px;
      @media (max-width: 480px){
         font-size: 22px;
         margin-bottom: 28px;
      }
   }
   &__swiper {
      .swiper-wrapper {
         transition: linear;
         display: flex;
         align-items: center;
      }
      .swiper-slide {
         display: flex;
         align-items: center;
         justify-content: center;
         height: 100%;
         @media (max-width: 480px){
            max-width: 126px;
            width: 100%;
         }
         img{
            width: 100%;
            height: auto;
         }
      }
   }
}
