.section__work-projects {
   .work {
      &__body {
         display: flex;
         flex-direction: column;
         justify-content: center;
         align-items: center;
         gap: 88px;
         @media (max-width: 992px){
            gap: 110px !important;
         }
         @media (max-width: 480px){
            margin-bottom: 120px !important;
         }
      }
      &__item {
         display: flex;
         justify-content: center;
         align-items: center;
         gap: 30px;
         @media (max-width: 992px){
            flex-direction: column !important;
            max-width: 500px;
         }
         &-content{
            max-width: 542px;
            width: 100%;
         }
         &-img{
            text-align: center;
            min-width: 543px;
            width: 100%;
            height: 464px;
            @media (max-width: 768px){
               min-width: 100%;
            }
            @media (max-width: 480px){
               height: 352px !important;
            }
            img{
               object-fit: cover;
               object-position: top;
            }
            @media (max-width: 992px){
               img{
                  padding-bottom: 28px;
               }
            }
         }
         &-btn{
            @media (max-width: 992px){
               display: none;
               &.hide{
                  display: inline-flex !important;
               }
            }
            &.hide{
               display: none;
            }
         }
         &-title {
            font-weight: 700 !important;
            font-size: 40px !important;
            line-height: 1.5;
            color: #0E0E2C !important;
            margin-bottom: 28px;
            @media (max-width: 480px){
               font-size: 28px;
            }
            @media (max-width: 400px){
               font-size: 22px;
            }
         }
         &-text{
            color: rgba(14, 14, 44, 0.6) !important;
            max-width: 480px;
            width: 100%;
         }
         &.reverse {
            flex-direction: row-reverse;
         }
      }
   }
}
.work {
   &__header {
      max-width: 540px;
      width: 100%;
      margin-bottom: 69px;
   }
   &__body {
      display: grid;
      grid-template-columns: repeat(3, 1fr);
      grid-template-rows: repeat(2, 1fr);
      grid-column-gap: 30px;
      grid-row-gap: 19px;
      width: 100%;
      margin-bottom: 85px;
      @media (max-width: 768px) {
         grid-template-columns: repeat(2, 1fr);
         grid-template-rows: repeat(3, 1fr);
         grid-column-gap: 19px;
      }
      @media (max-width: 480px) {
         display: flex;
         flex-direction: column;
         gap: 19px;
         margin-bottom: 56px;
      }
   }
   &__item {
      &:first-child {
         grid-area: 1 / 1 / 2 / 2;
      }
      &:nth-child(2) {
         grid-area: 1 / 2 / 2 / 3;
      }
      &:nth-child(3) {
         grid-area: 1 / 3 / 3 / 4;
         @media (max-width: 768px) {
            grid-area: 3 / 1 / 4 / 3;
         }
      }
      &:nth-child(4) {
         grid-area: 2 / 1 / 3 / 2;
         @media (max-width: 768px) {
            grid-area: 2 / 2 / 3 / 3;
         }
      }
      &:nth-child(5) {
         grid-area: 2 / 2 / 3 / 3;
         @media (max-width: 768px) {
            grid-area: 2 / 1 / 3 / 2;
         }
      }
   }
   &__item-img {
      width: 100%;
      height: auto;
      @media (max-width: 768px) {
         height: 272px;
         img {
            object-fit: cover;
            object-position: top;
         }
      }
      img {
         width: 100%;
         height: 100%;
      }
   }
   &__item-title {
      font-weight: 700 !important;
      color: #3b71fe !important;
      margin-top: 9px;
   }
   &__item-text {
      font-weight: 400;
      font-size: 14px;
      line-height: 1.5;
      color: rgba(14, 14, 44, 0.8);
   }
   &__button {
      display: flex;
      justify-content: center;
   }
}
