.section {
   &__hero {
      padding: 83px 0 160px !important;
      @media (max-width: 768px) {
         padding: 28px 0 120px !important;
      }
      &-about {
         .hero {
            justify-content: center;
         }
         .hero__item:first-child {
            text-align: center;
            max-width: 768px;
            &::after {
               display: none;
            }
         }
         .hero__buttons {
            justify-content: center;
         }
      }
      &-projects {
         .hero {
            justify-content: center;
            &__count {
               display: flex;
               align-items: center;
               justify-content: center;
               gap: 33px;
               margin-bottom: 38px;
               @media (max-width: 768px){
                  flex-wrap: wrap;
                  gap: 58px;
               }
               &-item {
                  padding: 8px;
                  text-align: left;
                  @media (max-width: 768px){
                     max-width: 160px;
                     width: 100%;                   
                  }
                  @media (max-width: 400px){
                     max-width: 100%;
                     text-align: center;
                     display: flex;
                     flex-direction: column;
                     align-items: center;
                  }
               }
               &-title {
                  font-weight: 700;
                  font-size: 36px;
                  line-height: 171.5%;
                  color: #0e0e2c;
               }
               &-subtitle{
                  color: #3B71FE !important;
                  font-weight: 600 !important;
               }
            }
         }
         .hero__item:first-child {
            text-align: center;
            max-width: 768px;
            &::after {
               display: none;
            }
         }
         .hero__buttons {
            justify-content: center;
         }
      }
   }
}
.hero {
   display: flex;
   justify-content: space-between;
   align-items: center;
   @media (max-width: 992px) {
      justify-content: center;
   }
   &__item {
      &:first-child {
         max-width: 530px;
         width: 100%;
         position: relative;
         @media (max-width: 992px) {
            max-width: 100%;
         }
         &::after {
            content: url(..//img/hero-arrow-down.svg);
            position: absolute;
            left: -110px;
            top: 20px;
            width: 100%;
            height: auto;
            z-index: -1 !important;
            @media (max-width: 992px) {
               display: none;
            }
         }
      }
      &:nth-child(2) {
         @media (max-width: 992px) {
            display: none;
         }
         img {
            width: 100%;
            height: auto;
         }
      }
   }
   &__title {
      font-weight: 700;
      font-size: 48px;
      line-height: 1.56;
      color: #0e0e2c;
      margin-bottom: 28px;
      @media (max-width: 768px) {
         font-size: 40px;
         margin-bottom: 32px;
      }
      @media (max-width: 350px) {
         font-size: 35px;
      }
   }
   &__text {
      color: rgba(14, 14, 44, 0.6) !important;
      margin-bottom: 54px;
      @media (max-width: 480px) {
         margin-bottom: 24px;
      }
   }
   &__buttons {
      display: flex;
      align-items: center;
      gap: 51px;
      @media (max-width: 480px) {
         flex-direction: column;
         justify-content: center;
         gap: 24px;
         .hero__btn {
            width: 100%;
         }
      }
   }
   &__link {
      display: inline-flex;
      align-items: center;
      gap: 8px;
      font-weight: 500;
      margin-bottom: 0 !important;
      img {
         transition: all 0.3s ease;
      }
      &:hover {
         img {
            transform: translate(8px, 0);
            transition: all 0.3s ease;
         }
      }
   }
}
