.section {
   &__contact {
      padding: 31px 0 180px !important;
      background: url(..//img/contact-bg.svg) center no-repeat;
      background-size: fill;
      @media (max-width: 768px){
         padding: 28px 0 120px !important;
         background: none;
      }
   }
}
.contact {
   &__header {
      text-align: center;
      margin-bottom: 35px;
      @media (max-width: 480px){
         text-align: left;
      }
   }
   &__form {
      display: flex;
      flex-direction: column;
      justify-content: center;
      align-items: center;
      max-width: 537px;
      width: 100%;
      gap: 16px;
      margin: 0 auto;
   }
   &__form-item {
      display: flex;
      flex-direction: column;
      width: 100%;
      gap: 10px;
   }
   &__form-label {
      font-weight: 600;
      font-size: 14px;
      line-height: 21px;
      color: #0e0e2c;
   }
   &__form-input {
      width: 100%;
      background: #ffffff;
      border: 1px solid #767676;
      border-radius: 4px;
      padding: 8px 16px;
      &::placeholder{
         color: #B1B1B1;
      }
      &::-moz-placeholder{
         color: #B1B1B1;
      }
   }
   &__form-textarea {
      resize: none;
      height: 176px;
      font-family: 'Poppins', sans-serif;
   }
   &__form-btn {
      border: none;
      svg{
         transition: all .3s ease;
         path{
            stroke: #ffffff;
         }
      }
      &:hover{
         svg{
            transform: translate(8px, 0);
            transition: all .3s ease;
         }
      }
   }
}
