*,
*::before,
*::after {
	box-sizing: border-box;
}

html,
body {
	position: relative;
}

body {
	min-height: 100vh;
	height: 100%;
	display: flex;
	flex-direction: column;
	overflow-x: hidden !important;
	font-family: "Poppins", sans-serif;
	font-style: normal;
	opacity: 0;
	transition: all .3s ease;
	&.loaded{
		opacity: 1;
		transition: all .3s ease;
	}
}

main {
	flex: 1 1 auto;
}

ul,
ol,
li,
p,
h1,
h2,
h3,
h4,
h5,
h6 {
	margin: 0;
	padding: 0;
	list-style: none;
}
a {
	text-decoration: none;
}
* {
	margin: 0;
	padding: 0;
	box-sizing: border-box;
}
:focus-visible {
	outline: none;
}
.container {
	max-width: $mw;
	margin: 0 auto;
	width: 100%;
	@media (max-width: $md1) {
		max-width: 970px;
	}
	@media (max-width: $md2) {
		max-width: 750px;
	}
	@media (max-width: $md3) {
		max-width: none;
		padding: 0 10px;
	}
}
.section {
	padding: 0 0 160px;
	@media (max-width: 768px) {
		padding: 0 0 120px;
	}
}
.title {
	font-weight: 700;
	font-size: 34px;
	line-height: 1.5;
	color: #0e0e2c;
	@media (max-width: 480px) {
		font-size: 22px;
	}
}
.subtitle {
	font-weight: 600;
	font-size: 22px;
	line-height: 171.5%;
	color: #3b71fe;
	margin-bottom: 19px;
	@media (max-width: 480px) {
		margin-bottom: 16px;
		font-size: 16px;
	}
}
.text {
	font-weight: 400;
	font-size: 16px;
	line-height: 24px;
	color: rgba(14, 14, 44, 0.6);
}
.btn {
	font-weight: 600;
	font-size: 14px;
	line-height: 1.5;
	color: #ffffff;
	background: #3b71fe;
	border-radius: 4px;
	padding: 16px 24px;
	display: inline-flex;
	align-items: center;
	justify-content: center;
	gap: 10px;
	transition: all 0.3s ease;
	&:hover {
		background: #2951b7;
		transition: all 0.3s ease;
	}
}
.btn__outline {
	display: inline-flex;
	justify-content: center;
	align-items: center;
	gap: 10px;
	font-weight: 600;
	font-size: 14px;
	line-height: 1.5;
	color: #3b71fe;
	border: 2px solid #3b71fe;
	border-radius: 4px;
	padding: 18px 24px;
	transition: all 0.3s ease;
	svg {
		transition: all 0.3s ease;
		path {
			transition: all 0.3s ease;
		}
	}
	&:hover {
		transition: all 0.3s ease;
		background: #3b71fe;
		color: #fff;
		svg {
			transform: translate(5px, 0);
			transition: all 0.3s ease;
			path {
				stroke: #fff;
				transition: all 0.3s ease;
			}
		}
	}
}

body.dark-theme {
	background: #0e0e2c;
	.title,
	.subtitle,
	.text,
	h1,
	h2,
	h3,
	h4,
	h5,
	h6,
	p,
	.contact__form-label, .work__item-title {
		color: #ffffff !important;
	}
	.btn__outline {
		color: #ffffff;
		border: 2px solid #ffffff;
		path {
			stroke: #ffffff;
		}
		&:hover {
			background: #ffffff;
			color: #0e0e2c;
			path {
				stroke: #0e0e2c;
			}
		}
	}
	nav {
		background: #0e0e2c;
	}
	.nav__menu.active {
		background: #0e0e2c;
	}
	.nav__list-link {
		&::after {
			background: #ffffff;
		}
		&.active::after {
			background: #ffffff;
		}
	}
	.contact__form-input {
		background: rgba(255, 255, 255, 0.2);
		border: 1px solid #767676;
	}
	.accordion__item {
		border: 2px solid #ffffff;
		.line1,
		.line2 {
			background: #ffffff;
		}
	}
	.burger::after,
	.burger::before,
	.burger span {
		background: #ffffff;
	}
	footer {
		background: #0e0e2c;
	}
}
