nav {
   &.fixed__nav {
      position: fixed;
      left: 0;
      top: 0;
      right: 0;
      z-index: 99;
      background-color: #ffffff;
      .nav {
         padding: 12px 0;
         transition: all 0.3s ease;
      }
   }
}
.nav {
   display: flex;
   align-items: center;
   justify-content: space-between;
   padding: 6px 0;
   transition: all 0.3s ease;
   @media (max-width: 992px) {
      position: relative !important;
   }
   &__logo {
      display: flex;
      align-items: center;
   }
   &__menu {
      display: flex;
      justify-content: space-between;
      align-items: center;
      width: 100%;
      @media (max-width: 992px) {
         display: none;
         &.active {
            display: flex;
            justify-content: center;
            flex-direction: column;
            width: 100%;
            height: 100vh;
            position: fixed;
            gap: 50px;
            left: 0;
            top: 0;
            bottom: 0;
            right: 0;
            background: #ffffff;
            z-index: 99;
         }
      }
   }
   &__list {
      display: flex;
      justify-content: center;
      align-items: center;
      gap: 32px;
      margin: 0 auto;
      @media (max-width: 992px) {
         flex-direction: column;
         gap: 20px;
      }
   }
   &__list-link {
      position: relative;
      @media (max-width: 992px) {
         font-size: 24px;
      }
      &::after {
         content: "";
         width: 100%;
         height: 2px;
         position: absolute;
         bottom: -1px;
         left: 0;
         background-color: #3b71fe;
         transform: translate(-50%, 0) scaleX(0);
         transition: all 0.3s ease;
      }
      &:hover::after {
         transform: translate(0, 0) scaleX(1);
         transition: all 0.3s ease;
      }
      &:hover {
         color: #0e0e2c;
         transition: all 0.3s ease;
      }
      &.active {
         color: #0e0e2c;
         &::after {
            content: "";
            width: 28px;
            height: 2px;
            position: absolute;
            bottom: -1px;
            left: 0;
            background-color: #3b71fe;
            transform: translate(0, 0) scaleX(1);
         }
         &:hover::after {
            width: 100%;
         }
      }
   }
   &__btn {
      @media (max-width: 992px) {
         font-size: 20px;
      }
   }
   .burger {
      @media (max-width: 992px) {
         width: 25px;
         height: 24px;
         z-index: 1001;
         position: absolute;
         right: 0;
         &::after,
         &::before {
            content: "";
            position: absolute;
            width: 18px;
            height: 2px;
            border-radius: 2px;
            left: 50%;
            transform: translate(-50%, 0);
            background-color: #0e0e2c;
            transition: all 0.3s ease;
         }
         &.active-burger::before {
            transform: rotate(45deg);
            top: 12px;
            width: 100%;
            left: 0;
         }
         &.active-burger::after {
            transform: rotate(-45deg);
            bottom: 10px;
            width: 100%;
            left: 0;
         }
         &::before {
            top: 6px;
         }
         &::after {
            bottom: 6px;
         }
         span {
            position: absolute;
            background-color: #0e0e2c;
            right: 3px;
            width: 14px;
            height: 2px;
            top: 50%;
            transform: translate(0, -50%);
            transition: all 0.3s ease;
            border-radius: 2px;
         }
         &.active-burger span {
            transform: scale(0);
         }
      }
   }
}
